import React from "react";
import {Marker, Popup} from 'react-leaflet';
import { Icon } from "leaflet";
export class SitesMarkers extends React.Component 
{
    icon=new Icon({
        iconUrl: "https://unpkg.com/leaflet@1.6.0/dist/images/marker-icon.png",
        iconSize: [15, 15]
      });
    
    render()
    {
     return this.props.sites === null ? null : (
        this.props.sites.map(site => (
        <Marker
          key={site.id}
          position={[
            site._coordinate.lat,
            site._coordinate.lng
          ]}
          icon={this.icon}
        >
          <Popup>
            <div>
              <b>{site.address}</b>   {site.state}, {site.postalcode}<br />           
              <b>panels:</b> {site.panels}<br />
              <b>capacity:</b> {`${site.capacity} (watt-hour)`}<br />
              ({site._coordinate.lat},{site._coordinate.lng})<br />
              <a href={"#/site/" + site.id}>view</a>
            </div>
          </Popup>
        </Marker>
  
      ))
    )
   }
  }