import  React  from 'react';
import { SemiCircle } from 'react-leaflet-semicircle';
export class NearByGeoMarker extends React.Component
{
  
render()
{
  return(
<SemiCircle
        position={this.props.center}
        radius={(this.props.radiusUnit==='km')?this.props.radius*1000:this.props.radius*1609.34}
        startAngle={0}
        stopAngle={360}
        >
      
      </SemiCircle>
  )
}
}