import React from 'react'
import styled, { ThemeProvider } from 'styled-components'
import { Grid, Cell as BaseCell, BREAKPOINTS } from '@mollycule/lattice'
import TextField from '@mui/material/TextField';
import './Search.css';
import SearchIcon from '@mui/icons-material/Search';
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import Chip from '@mui/material/Chip';
import DeleteIcon from '@mui/icons-material/Delete';
import { ImportProgress } from './ImportProgress';


const Cell = styled(BaseCell)`
  min-height: 100px;
 
`

export class Search extends React.Component {
  constructor(props) {
    super(props);
     this.handleLatitudeTextChange = this.handleLatitudeTextChange.bind(this);
    this.handleLongitudeTextChange = this.handleLongitudeTextChange.bind(this);
    this.handleRadiusTextChange = this.handleRadiusTextChange.bind(this);
    this.handleRadiusUnitTextChange = this.handleRadiusUnitTextChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleImport = this.handleImport.bind(this)
    this.RemoveFilter = this.RemoveFilter.bind(this);
    this.validateLatitude = this.validateLatitude.bind(this);
    this.validateLongitude = this.validateLongitude.bind(this);
    this.validateRadius = this.validateRadius.bind(this);
    this.state={
                error:false,
                helperText:''}
  }

  RemoveFilter(e) {
    this.props.OnRemoveFilter(e);
  }
  handleLatitudeTextChange(e) {
    this.props.onLatitudeTextChange(e.target.value);
  }
  handleLongitudeTextChange(e) {
    this.props.onLongitudeTextChange(e.target.value);
  }
  handleRadiusTextChange(e) {
    this.props.onRadiusTextChange(e.target.value);
  }
  handleRadiusUnitTextChange(e) {
    this.props.onRadiusUnitTextChange(e.target.value);
  }
  handleImport(event) {
    event.preventDefault();
    console.log("importing search");
    this.props.OnImport(event);
  };
  handleSubmit(event) {
    event.preventDefault();
    if( this.validateLatitude()&& this.validateLongitude()&&this.validateRadius())
    {
      this.setState({
        helperText:'',
        error:false 
       }) ;
      this.props.OnSubmit(event);
    }
    else
    this.setState({
      helperText:' * invalid coordinates',
      error:true 
     }) ;
  }
  validateLatitude(){
    return isFinite(this.props.latitude) && Math.abs(this.props.latitude) <= 90;
  }
  validateLongitude() {
    return isFinite(this.props.longitude) && Math.abs(this.props.longitude) <= 180;
  }
  validateRadius() {
    return  /^[1-9]\d*$/.test(this.props.radius)?true:false;
  }
  render() {
    const filtred = this.props.filtred;
    return (
      <ThemeProvider theme={{ breakpoints: BREAKPOINTS }}>
        <FormControl  error={this.state.error}>
          <Grid
            width='70vw'
            mx='auto'
            gap='13px'
            cols={{ md: 6 }}
            alignContent='center'
            alignItems='center'
            justifyContent='center'
            justifyItems='start'
          >

            <Cell >
              <TextField
                required 
                id="latitude"
                label="latitude"
                variant="filled"
                value={this.props.latitude}
                onChange={this.handleLatitudeTextChange}
               

              />
            </Cell>
            <Cell >
              <TextField
                required
                id="longitude"
                label="longitude"
                variant="filled"
                value={this.props.longitude}
                onChange={this.handleLongitudeTextChange}
                
              />
            </Cell>
            <Cell>
              <TextField
                id="radius"
                required
                label="radius"
                type="number"
                InputLabelProps={{
                  shrink: true,
                }}
                min="10"
                max="100"
                variant="filled"
                value={this.props.radius}
                onChange={this.handleRadiusTextChange}
                

              />
            </Cell>
            <Cell >
              <FormControl fullWidth>
                <InputLabel id="radiusUnit-label">radiusUnit</InputLabel>
                <Select
                  labelId="radiusUnit-label"
                  id="radiusUnit"
                  label="radiusUnit"
                  value={this.props.radiusUnit}
                  onChange={this.handleRadiusUnitTextChange}
                >
                  <MenuItem value={"km"}>KM (Kilometers)</MenuItem>
                  <MenuItem value={"mi"}>MI (Miles)</MenuItem>
                </Select>
              </FormControl>
            </Cell>
            <Cell >
              <Button variant="contained" type="submit" onClick={this.handleSubmit} endIcon={<SearchIcon />} size="meduim">Fetch Sites</Button>
            </Cell>
            <Cell >
            <ImportProgress  OnImport={this.handleImport}/>
            </Cell>
          </Grid>
          <FormHelperText>{this.state.helperText}</FormHelperText>
        </FormControl>
        {filtred ? <Chip
          label="remove filter"
          onDelete={this.RemoveFilter}
          deleteIcon={<DeleteIcon />}
        /> : null}
      </ThemeProvider>



    )
  }

}