import * as React from 'react';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import {Search} from './Search';
import {Map} from './Map';
import {SitesTable} from './SitesTable';
import axios from 'axios';

export class Sites extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      center:this.props.center,
      imported:false,
      progress:0,
      latitude:'',
      longitude:'',
      radius:'',
      radiusUnit:'km',
      sites:[],
      filtred:false
    };
    this.handleLatitudeTextChange = this.handleLatitudeTextChange.bind(this);
    this.handleLongitudeTextChange = this.handleLongitudeTextChange.bind(this);
    this.handleRadiusTextChange = this.handleRadiusTextChange.bind(this); 
    this.handleRadiusUnitTextChange = this.handleRadiusUnitTextChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this); 
    this.handleImport = this.handleImport.bind(this); 
    this.removeFilter = this.removeFilter.bind(this);        
}
componentDidMount() {
  axios.get(`/api/v1/sites`)
    .then(res => {
       this.setState({ sites: res.data});
    }).catch((error) => {
      console.log(error); 
    });
}
componentDidUpdate() {
   const timer = setInterval(() => {
   if(!(this.state.latitude || this.state.longitude || this.state.radius )&& this.state.importing)
   {
     axios.get(`/api/v1/sites`)
      .then(res => {
        if(this.state.importing && this.state.progress <= 90)
        this.setState({progress:this.state.progress + 10})
        else
        this.setState({ importing:false});
        this.setState({ sites: res.data});
         
      }).catch((error) => {
        console.log(error); 
      });
    }}, 800);
    return () => {
      clearInterval(timer);
     
    };
  }

handleLongitudeTextChange(filterText) {
  this.setState({
    longitude: filterText
  });
}
handleRadiusTextChange(filterText) {
  this.setState({
    radius: filterText
  });
}
handleRadiusUnitTextChange(filterText) {
  this.setState({
    radiusUnit: filterText
  });
}
handleLatitudeTextChange(filterText) {
  this.setState({
    latitude: filterText
  });
}
handleImport()
{
  console.log("importing site");
  this.setState({
    importing: true
  });
}
handleSearch()
{
  axios.get(`/api/v1/sitesnearby?lat=${this.state.latitude}&lng=${this.state.longitude}&radius=${this.state.radius}&radiusUnit=${this.state.radiusUnit}`)
    .then(res => {
       this.setState({center:[this.state.latitude,this.state.longitude],sites: res.data, filtred:true})
    }).catch((error) => {
     console.log(error)
      alert('Sites Not Found')
    });
}
removeFilter()
{ 
 axios.get(`/api/v1/sites`)
.then(res => {
  this.setState({  
  sites: res.data,
  latitude:'',
    longitude:'',
    radius:'',
   radiusUnit:'km',
    filtred:false
});
})
.catch((error) => {
  console.log(error); 
});
}


render(){
 
     return(
        <Container maxWidth="lg" sx={{
          backgroundColor: (theme) =>
            theme.palette.mode === 'light'
              ? theme.palette.grey[100]
              : theme.palette.grey[900],
          flexGrow: 1,
          height: '100vh',
          overflow: 'auto',
        }}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>
                <Search latitude={this.state.latitude} 
                        longitude={this.state.longitude} 
                        radius={this.state.radius} 
                        radiusUnit={this.state.radiusUnit}
                        onLatitudeTextChange={this.handleLatitudeTextChange}
                        onLongitudeTextChange={this.handleLongitudeTextChange}
                        onRadiusTextChange={this.handleRadiusTextChange}
                        onRadiusUnitTextChange={this.handleRadiusUnitTextChange}
                        OnSubmit={this.handleSearch}
                        OnImport={this.handleImport}
                        filtred={this.state.filtred}
                        OnRemoveFilter={this.removeFilter}>
                        </Search>
              </Paper>
            </Grid>
            <Grid item xs={7}>
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>
                <Map
                center = {this.state.center} 
                zoom = {this.props.zoom} 
                sites={this.state.sites}
                latitude={this.state.latitude} 
                longitude={this.state.longitude} 
                radius={this.state.radius}
                filtred={this.state.filtred}  
                radiusUnit={this.state.radiusUnit}
                 ></Map>
              </Paper>
            </Grid>
            <Grid item xs={5}>
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>
                <SitesTable sites={this.state.sites}/>
              </Paper>
            </Grid>
          </Grid>
        </Container>
     );
   }
}

