import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TablePagination from '@mui/material/TablePagination';
import Title from './Title';
export class SitesTable extends React.Component {
   constructor(props) {
    super(props);    
    this.state = {
      page: 0,
      rowsPerPage: 10      
    };
  }
  preventDefault(event) {
    event.preventDefault();
  };
  handleChangePage = (event, newPage) => {
    this.setState({page:newPage});
  };
  handleChangeRowsPerPage = (event) => {
    this.setState({rowsPerPage:+event.target.value});
    this.setState({page:0});
  };
  render() {
    return (
      <React.Fragment>
        <Title>Sites</Title>
        <Table sx={{ minWidth: 300 }} size="small">
          <TableHead>
            <TableRow>
              <TableCell>id</TableCell>              
              <TableCell>Lat</TableCell>
              <TableCell>Lng</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.props.sites.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage)
              .map((row) => {
                return (
                  <TableRow key={row.id}>
                    <TableCell>{row.id}</TableCell>
                     <TableCell>{row._coordinate.lat}</TableCell>
                    <TableCell>{row._coordinate.lng}</TableCell>

                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
        <TablePagination
          rowsPerPageOptions={[10, 25, 100]}
          component="div"
          count={this.props.sites.length}
          rowsPerPage={this.state.rowsPerPage}
          page={this.state.page}
          onPageChange={this.handleChangePage}
          onRowsPerPageChange={this.handleChangeRowsPerPage}
        />
      </React.Fragment>
    );
  }
}