import React from "react";
import { MapContainer,MapConsumer, TileLayer } from 'react-leaflet';
import './Map.css'
import { Box } from '@mollycule/lattice';
import { SitesMarkers } from './SitesMarkers';
import {NearByGeoMarker} from './NearByGeoMarker';
export class Map extends React.Component  {
 
   render() { 
    
     return (
      <div>        
        <Box
          component="form"
          noValidate
          autoComplete="off"
        >
       <MapContainer center={this.props.center} zoom={this.props.zoom}  dragging={true}
        doubleClickZoom={false} 
        scrollWheelZoom={false}>
         <MapConsumer>
        {(map) => {
         map.setView(this.props.center);
         return null;
        }}
      </MapConsumer>
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            />
            <SitesMarkers sites={this.props.sites} latitude={this.props.latitude} longitude={this.props.longitude} 
                redius={this.props.radius}  rediusUnit={this.props.rediusUnit}/>
            
            {this.props.filtred ?<NearByGeoMarker  center={this.props.center}
        pathOptions={this.props.pathOptions}
        radius={this.props.radius} rediusUnit={this.props.rediusUnit}/>:null}
          </MapContainer>         
        </Box>        
      </div>
    )
  }
}


