import * as React from 'react';
import LinearProgress from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import DataObject from '@mui/icons-material/DataObject';
import axios from 'axios';
class ProgressBar extends React.Component{
  render()
  {
  return (
  <Box sx={{ width: '100%' }}>
  <Box sx={{ display: 'flex', alignItems: 'center' }}>
    <Box sx={{ width: '100%', mr: 1 }}>
      <LinearProgress variant="determinate" value={this.props.progress}/>
    </Box>
    <Box sx={{ minWidth: 35 }}>
      <Typography variant="body2" color="text.secondary">{`${Math.round(
        this.props.progress,
      )}%`}</Typography>
    </Box>
  </Box>
  </Box>
  )
   }

 }
export class ImportProgress extends React.Component {
 
  constructor(props)
 {
   super(props);
   this.state={progress:0,import_data:false,isImporting:false};
   this.handleImport=this.handleImport.bind(this);
 }
 handleImport(event) {
  event.preventDefault();
  console.log("importing progress");
  this.setState({isImporting:true});
  axios.get(`/api/v1/import`)
  .then(res => {
   this.setState({import_data:true,isImporting:false})
   })
  .catch((error) => {
    console.log(error); 
 });
 this.props.OnImport(event);
}
 componentDidUpdate(){
     const timer = setInterval(() => {
      if(this.state.isImporting && this.state.progress <= 90)
      this.setState({progress:this.state.progress + 10})                      
      }, 800);
    return () => {
      clearInterval(timer);
     
    };
    }
   
    

  render()
{
  return (
    <div>
      <Button onClick={this.handleImport} variant="contained"  endIcon={<DataObject/>}>import Sites</Button>
    {this.state.isImporting?<ProgressBar progress={this.state.progress}/>:null}
    </div>
  );
}
}

